.navigation{
    @extend %mixin-card;

}

.navigation-list{
    margin: 0;
    padding: 0;
    list-style: none;
}

.navigation-item{
    display: flex;
    align-items: center;
    height: 60px;
    border-bottom: 2px solid #f5f5f5;
    color: #808080;
    
    svg{
        fill: #ccc;
        transition: 0.2s fill ease-in;
    }

    &:hover{
        color:$blue;
        cursor: pointer;
        svg{
            fill: $blue;
        }
    }
}

.navigation-item1{
    display: flex;
    justify-content: center;
    align-items: center;
    height: 60px;
    border-bottom: 2px solid #f5f5f5;
    color: #808080;
}

.navigation-item-icon{
    flex-shrink: 0;
    display: flex;
    align-items: center;
    justify-content: center;
}

.navigation-item-title{
    font-size: 13px;
    font-weight: 500;
    line-height: 1;
}

.navigation-item-arrow{
    margin-left: auto;
    padding-right: 20px;
}